        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <img src="../../../../static/img/home/progress.png" style="width: 40%;height: auto;margin: 30px 0">
    <div class="textarea" >
      <el-input  type="textarea" readonly v-model="desc" :autosize="{ minRows: 10}"></el-input>
    </div>
    <div style="margin: 20px 0">
      <el-radio v-model="radio" label="1"><span style="color: #707070">我已阅读并同意</span><span style="color: #F39800">《圆梦学海服务协议》</span></el-radio>
      <el-button type="primary" round style="width: 70%;margin-top: 15px;" @click="go('/registerlecturer')" :disabled="radio==null">下一步</el-button>
    </div>
  </div>


</template>

<script>
  import { Header } from 'mint-ui';
  export default {
    data () {
      return {
        radio: null,
        desc:"协议内容"
      }
    },
    methods:{
        go(e){
          this.$router.push(e)
        }
    },
    mounted(){

    },


  }
</script>


<style scoped>
